___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 498px) {
  .app-bar {
    padding: 6px; 
    flex-direction: column; 
  }

  .hero-image {
    max-height: 300px; 
  }

  .restaurants {
    grid-template-columns: repeat(auto-fit, minmax(100%, 1fr)); 
  }

  .restaurant-item__header__rating {
    padding: 4px 8px; 
  }

  #footerTools {
    padding: 10px; 
  }
}

@media screen and (min-width: 499px) {
  .app-bar {
    grid-template-columns: 1fr auto;
    padding: 8px 32px;
  }
 
  .app-bar .app-bar__brand h1 {
    font-size: 1.5em;
  }
 
  .app-bar .app-bar__menu {
    display: none;
  }
 
  .app-bar .app-bar__navigation {
    position: static;
    width: 100%;
  }
 
  .app-bar .app-bar__navigation ul li {
    display: inline-block;
  }
 
  .app-bar .app-bar__navigation ul li a {
    display: inline-block;
    width: 120px;
    text-align: center;
    margin: 0;
  }
 
  .restaurants {
    grid-template-columns: 1fr 1fr;
  }
 
  .restaurant {
    grid-template-columns: auto 1fr;
    
  }
 
  .restaurant .restaurant__title {

    grid-column-start: 1;
    grid-column-end: 3;
  }
 
  .restaurant .restaurant__overview {
    grid-column-start: 1;
    grid-column-end: 3;
  }

  #footerTools {
    flex-direction: row; 
    justify-content: center; 
  }

  #footerTools .btn {
    margin: 5px; 
    width: auto; 
  }

  footer {
    flex-direction: row; 
    justify-content: space-between; 
    padding: 20px 40px; 
  }

  footer .footer-content {
    flex: 1; 
    padding: 0 10px;
}
 
@media screen and (min-width: 800px) {
  .app-bar .app-bar__brand h1 {
    font-size: 2em;
  }
}
 
@media screen and (min-width: 850px) {
  .restaurants {
    grid-template-columns: repeat(3, 1fr);
  }
}
 
@media screen and (min-width: 1200px) {
  .restaurants {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }

  .hero-image{
    min-width: 1000px;
  }
}
 

} `, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA;EACE;IACE,YAAY;IACZ,sBAAsB;EACxB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,0DAA0D;EAC5D;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,aAAa;EACf;AACF;;AAEA;EACE;IACE,+BAA+B;IAC/B,iBAAiB;EACnB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,gBAAgB;IAChB,WAAW;EACb;;EAEA;IACE,qBAAqB;EACvB;;EAEA;IACE,qBAAqB;IACrB,YAAY;IACZ,kBAAkB;IAClB,SAAS;EACX;;EAEA;IACE,8BAA8B;EAChC;;EAEA;IACE,+BAA+B;;EAEjC;;EAEA;;IAEE,oBAAoB;IACpB,kBAAkB;EACpB;;EAEA;IACE,oBAAoB;IACpB,kBAAkB;EACpB;;EAEA;IACE,mBAAmB;IACnB,uBAAuB;EACzB;;EAEA;IACE,WAAW;IACX,WAAW;EACb;;EAEA;IACE,mBAAmB;IACnB,8BAA8B;IAC9B,kBAAkB;EACpB;;EAEA;IACE,OAAO;IACP,eAAe;AACnB;;AAEA;EACE;IACE,cAAc;EAChB;AACF;;AAEA;EACE;IACE,qCAAqC;EACvC;AACF;;AAEA;EACE;IACE,sCAAsC;EACxC;;EAEA;IACE,iBAAiB;EACnB;AACF;;;AAGA","sourcesContent":["@media screen and (max-width: 498px) {\r\n  .app-bar {\r\n    padding: 6px; \r\n    flex-direction: column; \r\n  }\r\n\r\n  .hero-image {\r\n    max-height: 300px; \r\n  }\r\n\r\n  .restaurants {\r\n    grid-template-columns: repeat(auto-fit, minmax(100%, 1fr)); \r\n  }\r\n\r\n  .restaurant-item__header__rating {\r\n    padding: 4px 8px; \r\n  }\r\n\r\n  #footerTools {\r\n    padding: 10px; \r\n  }\r\n}\r\n\r\n@media screen and (min-width: 499px) {\r\n  .app-bar {\r\n    grid-template-columns: 1fr auto;\r\n    padding: 8px 32px;\r\n  }\r\n \r\n  .app-bar .app-bar__brand h1 {\r\n    font-size: 1.5em;\r\n  }\r\n \r\n  .app-bar .app-bar__menu {\r\n    display: none;\r\n  }\r\n \r\n  .app-bar .app-bar__navigation {\r\n    position: static;\r\n    width: 100%;\r\n  }\r\n \r\n  .app-bar .app-bar__navigation ul li {\r\n    display: inline-block;\r\n  }\r\n \r\n  .app-bar .app-bar__navigation ul li a {\r\n    display: inline-block;\r\n    width: 120px;\r\n    text-align: center;\r\n    margin: 0;\r\n  }\r\n \r\n  .restaurants {\r\n    grid-template-columns: 1fr 1fr;\r\n  }\r\n \r\n  .restaurant {\r\n    grid-template-columns: auto